.card-list {
    width: 92vw;
    margin: 0 auto;
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    overflow-y: auto;
    overflow-x: hidden;
  }

  @media screen and (max-width: 600px) {
    .card-list {
      width: 100vw;
      row-gap: 20px;
    }
  }